@import "styles/variables.scss";

.withoutPB {
    padding-bottom: 0;
    margin-bottom: 93px;
}

.col {
    position: relative;
}

.col > .banner {
    margin-bottom: 0;
}

div.banner {
    transform: translateX(0px);
}

button.bannerBtn {
    position: absolute;
    bottom: 40px;
    right: 60px;
    width: 320px;
    height: auto;
}

.text {
    font-size: 25px;
    line-height: 126%;
    letter-spacing: -0.25px;
    margin-bottom: 45px;
    max-width: 1050px;
}

.text:last-of-type {
    margin-bottom: 105px;
}

.favorite {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.favorite > div {
    display: flex;
    align-items: flex-start;
}

.favorite > div > img {
    position: relative;
    top: -5px;
    margin-right: 8px;
    margin-bottom: 12px;
}

.favorite > div > p {
    font-size: 22px;
    line-height: 126%;
    letter-spacing: -0.223px;
    margin-bottom: 12px;
}

.favoriteImg {
    width: 100vw;
    height: auto;
    position: relative!important;
    margin-bottom: 110px;
}

.middleBanner {
    margin: auto;
    display: flex;
    align-items: center;
    width: 85%;
    padding: 19px;
    border: 1px solid rgba(35, 35, 35, 0.3);
    margin-bottom: 143px;
}

.middleBanner > img {
    margin-right: 42px;
}

.middleBanner > p {
    font-size: 35.912px;
    line-height: 126%;
    letter-spacing: -0.359px;
}

.conditions {
    margin-bottom: 150px;
}

.conditions > div {
    display: flex;
    align-items: center;
}

.conditions > div > p:first-child {
    font-size: 32px;
    line-height: 110%;
    letter-spacing: -0.64px;
    margin-right: 69px;
}

.conditions > div > p:last-child {
    font-size: 73px;
    line-height: 110%;
    letter-spacing: -1.46px;
}

.imgList {
    display: flex;
    margin-bottom: 93px;
}

.imgList > img {
    width: 31.52%;
    max-width: 360px;
    height: auto;
}

.imgList > img:not(:last-child) {
    margin-right: 31px;
}

.subTitle {
    max-width: 950px;
}

.text:last-of-type.disclaimer {
    margin-bottom: 74px;
}

.assortment {
    margin-bottom: 119px;
    font-size: 29.208px;
    line-height: 126%;
    letter-spacing: -0.292px;
}

.assortment > a {
    text-decoration: none;
    border-bottom: 1px solid $darkBlue;
    font-size: 29.208px;
    line-height: 126%;
    letter-spacing: -0.292px;
}

.emailBlock {
    display: flex;
    flex-direction: column;
    background-color: $darkBlue;
    padding: 72px 34px 56px;
    margin-top: 393px;
    transform: translateY(-300px);
}

.emailBlock > p {
    font-size: 64.92px;
    line-height: 93%;
    letter-spacing: -1.948px;
    margin-bottom: 78px;
    color: $beige;
}

.emailBlock > div {
    display: flex;
    align-items: center;
    width: 100%;
}

.modalContainer {
    padding: 78px 58px 98px 34px;
}

.emailBlock > div > div.emailInput,
div.modalContent > div.emailInput,
.emailBlock > div > input.emailInput,
div.modalContent > input.emailInput {
    width: 33%;
    background-color: $darkBlue;
    height: 50px;
    border: none;
    border-bottom: 1px solid $beige;
    padding: 0;
    color: $beige;
    font-size: 24.258px;
    line-height: 125%;
    letter-spacing: -0.485px;
}

div.modalContent > div.emailInput,
div.modalContent > input.emailInput {
    width: 100%;
    margin-bottom: 42px;
}

.emailBlock > div > div.emailInput > input,
div.modalContent > div.emailInput > input {
    background-color: $darkBlue;
    height: 50px;
    border-bottom: 1px solid $beige;
    padding: 0;
    color: $beige;
    font-size: 24.258px;
    line-height: 125%;
    letter-spacing: -0.485px;
}

.emailBlock > div input::placeholder,
div.modalContent input::placeholder {
    color: $beige;
    opacity: 0.4;
}

.emailBlock > div > *:not(:last-child),
div.modalContent > *:not(:last-child) {
    margin-right: 57px;
}

.emailBlock > div > img,
div.modalContent > div.sendBlock > img {
    cursor: pointer;
}

div.modalContent > p {
    position: relative;
    bottom: 32px;
}

p.errorTitle {
    color: $redColor;
    font-size: 30px;
}

h2.subTitleModal {
    color: $beige;
    margin-top: 0;
    line-height: 93%;
    letter-spacing: -1.948px;
}

.sendBlock {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.sendBlock > button {
    width: 320px;
}

@media screen and (max-width: 1680px) {
    .content {
        margin: 0 60px 93px;
    }
}

@media #{$tablet} {
    .content {
        margin: 60px 17px 82px;
    }
    div.banner {
        transform: translateX(0px);
    }
    button.bannerBtn {
        left: 15px;
        bottom: 180px;
        width: 211px;
    }
    .baseTitle {
        max-width: 230px;
    }
    .text {
        font-size: 16px;
        line-height: 126%;
        letter-spacing: -0.16px;
        margin-bottom: 28px;
    }
    .text:last-of-type {
        margin-bottom: 89px;
    }
    .favorite {
        grid-template-columns: 1fr;
    }
    .favorite > div > img {
        top: -10px;
        margin-right: 2px;
        margin-bottom: 20px;
    }
    .favorite > div > p {
        font-size: 16px;
        line-height: 126%;
        letter-spacing: -0.16px;
        margin-bottom: 20px;
    }
    .favoriteImg {
        margin-bottom: 89px;
    }
    .withoutMT {
        margin-top: 0;
    }
    .middleBanner {
        flex-direction: column;
        padding: 17px;
        margin-bottom: 71px;
    }
    .middleBanner > img {
        width: 100%;
        height: auto;
        margin: 0 0 25px;
    }
    .middleBanner > p {
        font-size: 16px;
        line-height: 126%;
        letter-spacing: -0.16px;
    }
    .conditions {
        margin-bottom: 92px;
    }
    .conditions > div {
        flex-direction: column;
        align-items: start;
    }
    .conditions > div:first-child {
        margin-bottom: 31px;
    }
    .conditions > div > p:first-child {
        font-size: 16px;
        line-height: 110%;
        letter-spacing: -0.32px;
        margin: 0 0 16px;
    }
    .conditions > div > p:last-child {
        font-size: 41px;
        line-height: 110%;
        letter-spacing: -0.82px;
    }
    .imgList {
        margin-bottom: 49px;
    }
    .imgList > img {
        width: 30%;
        height: auto;
    }
    .assortment {
        margin-top: 37px;
        font-size: 23.632px;
        line-height: 126%;
        letter-spacing: -0.236px;
    }
    .assortment > a {
        font-size: 23.632px;
        line-height: 126%;
        letter-spacing: -0.236px;
    }
    .emailBlock {
        padding: 46px 17px 59px;
        margin-top: 570px;
        transform: translateY(-450px);
    }
    .emailBlock > p {
        font-size: 39px;
        line-height: 93%;
        letter-spacing: -1.17px;
        margin-bottom: 68px;
    }
    .emailBlock > div {
        flex-direction: column;
        align-items: flex-end;
    }
    .emailBlock > div > div.emailInput,
    .emailBlock > div > input.emailInput {
        width: 100%;
    }
    .emailBlock > div > *:not(:last-child),
    div.modalContent > *:not(:last-child) {
        margin: 0 0 67px;
    }
    div.modalContent > p {
        margin-bottom: 0!important;
    }
    .emailBlock > div > div:last-of-type {
        margin-bottom: 19px;
    }
    div.modalContent > div.emailInput,
    div.modalContent > input.emailInput {
        margin-bottom: 18px;
    }
    div.modalContent > p {
        bottom: 10px;
    }
    p.errorTitle {
        font-size: 20px;
    }
    .sendBlock > button {
        width: 190px;
    }
}

@media #{$mobile} {
    .imgList {
        flex-direction: column;
    }

    .imgList > img {
        margin: 0 0 31px;
        width: 100%;
        max-width: 360px;
        height: auto;
    }
}
